.port-section{
  padding: 90px;
}

@media(max-width: 1024px){
  .port-section{
    padding: 30px;
  }
}


.top-banner{
  background-color: #0B2545;
  background-image: -webkit-linear-gradient(30deg, #0B2545 50%, #134074 50%);
 
  
}

.top-banner .inner{
  flex:1;
}

.port-main-title{
  color:white;
  
}

.port-main-image img{
  width: 600px;
}

.inner-wrapper{
  display: flex;
  flex-direction: row;
  align-items: center;
  
  max-width: 1300px;
  margin:0 auto;
}

.port-main-title h1{
  font-family: 'Open Sans', sans-serif;
  font-size: 85px;
  margin: 0px;
}

.inner-h2{
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  font-size: 65px;
  margin:0px;

}

.h2-dark{
  color: #13315C;
}

.inner{
  flex: 1;
}

.body-text{
  font-size: 25px;
}

.mid-class-2{
  background-color: #13315C;
  background-image: -webkit-linear-gradient(30deg, #134074 50%,  #0B2545 50%);

}

.center{
  text-align: center;
}

.stack-h2{
  color:white;
  text-align:center;
}

.card-container{
  margin:10px;
  background-color:white;
  border-radius: 5px;
  padding:30px;
}

.card-group{
  max-width: 1300px;
  margin: 0 auto;
  padding-top:60px;
}

.flex-cards{
  display: flex;

}

.card-container{
  flex: 1;
}

h2{
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
}

.card-h2-title{
  color: #13315C;
}


.project-parent{
  margin-top: 20px;
}

.project-container{
  background-color: #134074;
  background-image: -webkit-linear-gradient(30deg, #0B2545 40%, #134074 40%);
  border-radius: 5px;
}

.light-h2{
  color:white;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;

}

.inner-wrapper-align-top{
  display: flex;
  flex-direction: row;
  max-width: 1300px;
  margin:0 auto;
}

.light-p{
  color:white;
}

.project-body{
  padding: 50px;
}

.icon{
  background-color:white;
  border-radius: 100px;
  padding: 10px;
  margin: 10px;
}

.icon i{
  font-size: 30px;
  color: #134074;
}

.icon-list{
  display: flex;
}

.contact{
  color:white;
}

.inner-wrapper-contact{
  display: flex;
  flex-direction: row;
  margin:0 auto;
  max-width: 1500px;
}

.inner-wrapper-contact h2{
  text-align: center !important;
}

ul{
  font-size: 25px;
  color: #134074;
}

.resume-link a{
  text-decoration: none !important;
}

.email-me a{
  text-decoration: none !important;

}
.icon-list::-webkit-scrollbar {
  width: 20px;
}

.icon-list::-webkit-scrollbar-track {
  background-color: transparent;
}

.icon-list::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
  border: 6px solid transparent;
  background-clip: content-box;
}

@media(max-width: 1024px){
  .inner-wrapper{
    flex-direction: column;
  }

  .inner-wrapper-align-top {
    flex-direction: column;

  }

  .icon-list{
    overflow-x: scroll;
  }

  .flex-cards{
    flex-direction: column;
  }

  .inner-wrapper-contact {
    flex-direction: column;
  }

  .inner-h2{
    font-size: 30px;
  }

  .port-main-title h1{
    font-size: 45px;
  }

  .port-main-image img{
    width: 100%;
  }
}